.assets-list {
  margin-top: 0;
}

.assets-list__list,
.assets-list__entry {
  list-style: none;
  margin: 0;
  padding: 0;
}

.assets-list__entry {
  padding-bottom: 1rem;
}

.assets-list__headline {
  margin: 0;
  display: block;
}

.assets-list__figure {
  margin-bottom: 0.5em;
}

.assets-list__figure:last-child {
  margin-bottom: 0;
}

.assets-list__filesize:empty {
  display: none;
}

.assets-list__filesize::before {
  content: "(";
}

.assets-list__filesize::after {
  content: ")";
}

.assets-list__action {
  font-size: 12px;
  display: block;
}

.assets-list__link--text {
  display: inline-block;
}

.assets-list__list--thumbnail {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: stretch;
  margin-left: -15px;
  margin-right: -15px;
}

.assets-list__entry--thumbnail {
  padding: 0 15px 30px;
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 60px;
}

@media (min-width: 420px) {
  .assets-list__entry--thumbnail {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media (min-width: 576px) {
  .assets-list__entry--thumbnail {
    flex: 0 0 33%;
    max-width: 33%;
  }
}

.assets-list__link--thumbnail {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: flex-end;
}

.assets-list__figure {
  position: relative;
  display: block;
  overflow: hidden;
}

.assets-list__figure::before {
  content: "";
  padding-top: 75%;
  display: block;
}

.assets-list__img {
  position: absolute;
  display: block;
  top: 50%;
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  transform: translateY(-50%);
}
